
  import { defineComponent, computed } from 'vue'

  export default defineComponent({
      name: "Loader",
      props: {
        loading: {
            type: Boolean,
            default: false
        },
        text: {
          type: String,
            default: "Loading"
        },
        dark: {
            type: Boolean,
            default: false
        },
        classes: null,
        background: null,
        customLoader: null
    },
      setup(props) {
          const bc = computed(() => {
              return props.background || (props.dark ? 'rgba(0,0,0,0.8)' : 'rgba(255,255,255,0.8)')
          })
          return {
              bc
          }
      }
  })
