\n        </div>\n    </div>\n</template>\n\n<script lang=\"ts\">\nimport { defineComponent, computed, PropType} from \"vue\";\nimport { TranslationInterface } from \"@/models/settings/translation.interface\";\nimport router from \"../../../router\";\n\nexport default defineComponent({\n    name: \"Language\",\n    props: {\n        language: {\n            type: String,\n            required: true\n        },\n        main_language: {\n            type: String,\n            required: true\n        }\n    },\n    emits: ['select'],\n    setup(props, { emit }) {\n        const cssClass = computed(() => {\n            let css = 'item';\n            return css.trim();\n        })\n        const onClick = (is_reversed:boolean = false) => {\n            const result: TranslationInterface = {\n                translated: props.language,\n                main_language: props.main_language,\n                reverse_direction: is_reversed\n            }\n            emit('select', result);\n            router.push({\n                name: 'Words',\n                params: {\n                    parent: props.main_language,\n                    target: props.language,\n                    rev: is_reversed ? 'right' : 'left'\n                }\n            })\n        }\n        return {\n            cssClass,\n            onClick\n        }\n    }\n})\n</script>\n\n<style scoped>\n.w-full {\n    cursor: pointer;\n}\n</style>\n"],"sourceRoot":""}]);
